import { useState, useEffect } from "react";
import NewsItem from "./newsItem";
import style from './news.module.css';
const News = ({newsApi}) => {
    const [news, setNews] = useState(null);
    const [error, setError] = useState(null);
    const [isLoading,setIsloading] = useState(null);
    const [id, setId] = useState(1);
    const [value, onValue]= useState('');
    const [category, setCategory] = useState('all');
        useEffect(() => {
            setIsloading(false)
            newsApi
        .getAllNews()
        .then(articles => setNews(articles))
        .catch(error => setError(error))
        .finally(setIsloading(true))
        }, [newsApi])
        useEffect(() => {
            setIsloading(false)
            newsApi
            .getNewsbyPage(category,id)
            .then(articles => setNews(articles))
            .catch(error => setError(error))
            .finally(setIsloading(true))
        }, [id, newsApi])
        useEffect(() => {
            setIsloading(false)
            newsApi
            .getNewsbyPage(category, id)
            .then(articles => setNews(articles))
            .catch(error => setError(error))
            .finally(setIsloading(true))
        }, [category])
    const onId = (e) => {
        setId(Number(e.currentTarget.value));
    }
    const onCategory = (e) => {
        setCategory(e.currentTarget.textContent)
    }
    const onSubmitForm = (e) => {
        e.preventDefault();
        console.log(value)
        setCategory(value);
        onValue('');
    }
    const onInputForm = (e) => {
        onValue(e.currentTarget.value);
    }
    return(
        <div>
               <h1 className={style.news_title}>News</h1>
            <div className={style.category}>
                <div onClick={(e) => onCategory(e)} className={style.category_item}>All</div>
                <div onClick={(e) => onCategory(e)} className={style.category_item}>science</div>
                <div  onClick={(e) => onCategory(e)} className={style.category_item}>sports</div>
                <div  onClick={(e) => onCategory(e)} className={style.category_item}>dogs</div>
                <div  onClick={(e) => onCategory(e)} className={style.category_item}>cats</div>

            </div>
            <form className={style.news_form} onSubmit={onSubmitForm}>
                <input type="text" onChange={onInputForm} placeholder="please input your search" value={value}></input>
                <button type="submit">Submit</button>
            </form>
            <ul className={style.news}>
                {news && news.map(a => (
                   <NewsItem url={a.url} urlToImage={a.urlToImage} title={a.title} publishedAt={a.publishedAt} key={a.url}></NewsItem>
                ))}
            </ul>
            <div className={style.pagination}>
            <button className={style.pagitation_button} onClick={(e) => onId(e)} value='1'>1</button>
            <button className={style.pagitation_button}  onClick={(e) => onId(e)} value='2'>2</button>
            <button className={style.pagitation_button}  onClick={(e) => onId(e)} value='3'>3</button>
            <button className={style.pagitation_button}  onClick={(e) => onId(e)} value='4'>4</button>
            <button className={style.pagitation_button}  onClick={(e) => onId(e)} value='5'>5</button>
            </div>
        </div>
    )
}
export default News;